@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Card {
  margin: 10px;
  width: calc(100%/5 - 20px); }
  .Card__box {
    display: inline-block;
    position: relative; }
  .Card__image {
    width: 100%;
    border-radius: 5px;
    box-shadow: 0 2px 3px 2px #ccc; }
  .Card__box .Card__name {
    position: absolute;
    text-align: center;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 40%;
    width: 60%;
    z-index: 999; }

.EndScreen {
  text-align: center; }
  .EndScreen button {
    margin-top: 2rem;
    background-color: #316bd3;
    border: 2px solid #FFFFFF;
    color: #FFFFFF;
    text-transform: uppercase; }
  .EndScreen button:hover {
    background-color: #FFFFFF;
    border: 2px solid #316bd3;
    color: #316bd3; }

.Scoreboard h3 {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  margin: 1.5rem auto; }

.App {
  text-align: center; }
  .App__card-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 15%; }

h1 {
  font-family: "Lato", sans-serif;
  margin-bottom: 0; }

h2 {
  font-family: "Lato", sans-serif;
  font-size: 1.2rem;
  font-weight: 300;
  margin: 1rem auto; }

button {
  background-color: #FFFFFF;
  border: 2px solid #316bd3;
  border-radius: 0.3rem;
  box-shadow: none;
  color: #316bd3;
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  padding: 0.7rem 2.5rem;
  margin-bottom: 2rem; }

button:hover {
  background-color: #316bd3;
  border: 2px solid #FFFFFF;
  color: #FFFFFF; }

